.analytics {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 10%;
    background-color: black;
    color: white;

    .analytics-numbers {
        display: flex;
        align-items: flex-start;

        .analytics-number.top-five {
            .top-five-header {
                font-size: 16pt;
                text-align: center;
            }
            font-size: 14pt;
            text-align: left;
        }
        .analytics-number {
            text-align: center;
            align-items: flex-start;
            border: 1px solid white;
            color: white;
            padding: 20px;
            margin-right: 15px;
            font-size: 24pt;
            border-radius: 15px;
        }

        margin-bottom: 15px;
    }

    .analytics-table {
        display: flex;
        flex-direction: column;
        border: 1px solid white;
        border-radius: 15px;
        padding: 10px;
        width: 500px;

        .dimension-results {
            margin-top: 15px;
            max-height: 500px;
            overflow-y: scroll;
        }

        .dimension-stats.stats-header {
            border-bottom: 1px solid white;
        }
        
        .dimension-stats {
            display: flex;
            color: white;
            
            .ens-name {
                color: white;
            }
            .d-element {
                width: 190px;
                margin-left: 10px;
            }
        }
    }
}
