.playlist-creator.nft-jacker {
    padding: 5%;

    .save-panel {
        position: fixed;
        top: 10px;
        right: 10px;
        display: flex;

        .save-btn {
            background-color: red;
            color: white;
            padding: 10px;
            border-radius: px;
            margin-right: 10px;
        }
    }
    .creator-header {
        font-size: 16pt;
    }

    .playlist-songs {
        .song-container.selected {
            border: 1px solid white;
        }
        .song-container {
            border: 1px solid transparent;
            display: flex;
            margin: 1px;

            .song-number {
                padding: 4px;
                width: 30px;
                color: white;
                font-size: 10pt;
            }
        }
        .artist-name {
            display: flex;
            font-size: 10pt;
            width: 400px;
            padding: 4px;

            .song-name {
                font-size: 10pt;
                margin-left: 5px;
            }

            .cover-art {
                margin-left: auto;
                width: 20px;
                height: 20px;
                border-radius: 2px;
            }

        }
    }

    .add-btn {
        margin-top: 15px;
        background-color: white;
        color: black;
        font-size: 42pt;
        line-height: 50px;
        width: 50px;
        height: 50px;
        text-align: center;
    }

    .artist-name {
        background-color: #80808029;
        padding: 10px;
        position: relative;
        left: unset;
        right: unset;
        bottom: unset;
        top: unset;

        margin: unset;
        text-align: unset;
        width: 300px;

        .song-name {
            margin: unset;
            text-align: unset;
        }

        .cover-art {
            margin-top: 5px;
        }
    }
    

    .playlist-details {
        display: flex;

        .details-inputs {
            flex: 1;
            margin-right: 10%;
            max-width: 200px;
        }
    }

    .item-input {
        input.item-input-field {
            background-color: transparent;
            border: none;
            border-bottom: 1px solid white;
            outline: none;
            color: white;
            width: 100%;
        }
    }

    .add-item {
    }
}
