.z-macro {
    padding: 5px;
    background-color: gray;
    align-items: flex-start;
    width: 35px;
    .macro-slider {
        height: 120px;

        position: relative;
        width: 25px;
        background-color: #686565;

        .slider-indicator {
            position: absolute;
            background-color: #acd3f5;
            bottom: 0px;
            left: 0px;
            width: 25px;
        }
        
    }
}

