.nft-jacker.remix-viewer {
    height: 100%;
    width: 100%;
    overflow-y: scroll;
    overflow-x: hidden;
    background: linear-gradient(124deg,rgba(49,108,110,.8),rgba(54,52,103,.8) 50%,rgba(74,70,105,.8)),url(https://grainy-gradients.vercel.app/noise.svg)!important;

    .ying-yang-cover {
        position: absolute;
        top: 30px;
        right: 30px;
        width: 300px;
        height: 300px;
    }

    .remixes-list {
        margin: auto;
        width: 600px;

        .remix-option {
            display: flex;
            padding: 5px;
            .spore-id {
                margin-right: 15px;
            }
        }
    }
}


@media screen and (max-width: 600px) {
    .nft-jacker.remix-viewer {
        .remixes-list {
            margin-left: 10px;
        }
        .ying-yang-cover {
            width: 120px;
            height: 120px;
        }
    }
}
