.image-handler {
    width: 200px;
    height: 200px;
    display: flex;
    margin-right: 30px;
    border: 1px solid white;

    .drop-here {
        display: flex;
        width: 100%;
        height: 100%;

        .inner {
            width: 100%;
            height: 100%;

            img {
                object-fit: contain;
                width: 100%;
                height: 100%;
            }
        }

        span {
            margin: auto;
            text-align: center;
        }
    }
} 
