.remixes-page.nft-jacker {
    padding: 0px;
    overflow: scroll;
    color: white;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background: linear-gradient(45deg, rgb(200, 50, 50) 0%, rgb(50, 50, 50) 50%, rgb(100, 50, 50) 100%);

    .player-container {
        background-color: #ffffffa8;
    }

    .remixes-by-token {

        border-radius: 30px;
        
        box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
        display: flex;
        align-item: flex-start;
        padding: 20px;
        background-color: #171717;
        margin: 10px;
        width: 50%;
        min-width: 500px;
        margin-left: auto;
        margin-right: auto;

        .token-info {
            display: flex;
            flex-direction: column;
            width: 200px;

            .token-name {
                font-size: 10pt;
                margin-top: 15px;
            }


            img {
                width: 80px;
                height: 80px;
            }
        }

        .remix-option {
            font-size: 10pt;
            display: flex;
            border-radius: 15px;
            margin-left: 20px;
            padding: 4px;
            padding-left: 20px;
            padding-right: 20px;
            color: black;
            background-color: white;
            margin: 2px;

            .spore-id {
                width: 120px;
                margin-right: 10px;
            }
        }
    }
}
