.build-a-spore.nft-jacker {
    background: linear-gradient(45deg, rgb(200, 50, 50) 0%, rgb(50, 50, 50) 50%, rgb(100, 50, 50) 100%);

    .snap-btn {
        position: absolute;
        top: 25px;
        right: 25px;
        background-color: white;
        padding: 10px;
        color: black;
        border-radius: 15px;
    }

    .color-and-parameters {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
    }

    .text-and-mint {
        align-items: flex-start;
        position: absolute;
        left: 0;
        text-align: center;
        width: 430px;
        right: 0;
        margin-left: auto;
        margin-right: auto;
        bottom: 70px;

        .roadmap {
            font-size: 14pt;
            font-weight: 700;
            margin-bottom: 30px;
        }
    }

    .mint-button {
        width: 100px;
        margin-left: auto;
        margin-right: auto;
        align-items: flex-start;
        color: white;
        border-radius: 15px;
        cursor: not-allowed;
        background-color: #817e7e;
        opacity: 0.3;
        padding: 5px;
    }

    .center-builder {
        margin: auto;
        margin-top: 10%;
        display: flex;
    }
    iframe {
        border: none;
        opacity: unset;
        pointer-events:  unset;
        filter: unset;
        margin-right: 30px;
        box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    }
}
